export function factScoring(filename, fact,filters, method) {
    return axios({
        "method": "POST",
        "url": config.url.factScoring,
        "headers": {
            "Content-Type": "application/json; charset=utf-8"
        },
        "data": {
            "file_name": filename,
            "fact": fact,
            "filters":filters,
            "method": method,
        }
    })
}

export async function shareStory(jsonstring) {
    return new Promise((reslove, reject) => {
        axios({
            method: "post",
            url: `${config.url.share}`,
            config: {
                "headers": {
                    'Content-Type': 'application/json; charset=utf-8'
                },
            },
            data: {
                "share_json": jsonstring
            }
        }).then((response) => {
            if (response.status >= 400) {
                reject();
            } else if (response.status === 200 || response.status === 201 || response.status === 204) {
                reslove(response.data);
            } else {
                reject();
            }
        }).catch(error => {
            reject();
            //  message.error('error message');
        })
    })
}

